import ceoLogo from './img/ceo.svg'
import ctoLogo from './img/ceo.svg'
import jasonLogo from './img/ceo.svg'
import janiceLogo from './img/ceo.svg'
import danielLogo from './img/ceo.svg'
import down from '../../assets/img/down.svg'
import teamDesLogo from './img/team-des-logo.svg'
import Footer from '../../components/Footer/Footer'
import './team.less'
export default function Team({ lang }) {
  return (
    <div className='team-info'>
      <div className='team-bg-box'>
        <div className='team-title'>
          {lang['the-team-of-deri']}
        </div>
        <div className='team-describe'>
          {lang['team-des']}
        </div>
        <div className='hr'>
          <div className='long'></div>
          <div className='short'></div>
          <div className='middle'></div>
          <div className='down'>
            <img src={down} alt='' />
          </div>
        </div>
        <div className='team-box-figure-info one'>
          <div className='figure-info'>
            <div className='figure-logo ceo'>
            </div>
            <div className='figure-describe'>
              <div className='figure-describe-box'>
                <img src={teamDesLogo} alt='' />
                <div className='describe-box'>
                  <div> · {lang['ceo-des-one']}</div>
                  <div> · {lang['ceo-des-two']}</div>
                  <div> · {lang['ceo-des-three']}</div>
                  <div> · {lang['ceo-des-four']}</div>
                </div>
                <div className='name-job'>
                  <div className='name'>
                    {lang['0xAlpha']}
                  </div>
                  <div className='job'>
                    {lang['co-founder-and-ceo']}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='figure-info'>
            <div className='figure-logo cto'>

            </div>
            <div className='figure-describe'>
              <div className='figure-describe-box'>
                <img src={teamDesLogo} alt='' />
                <div className='describe-box'>
                  <div> · {lang['cto-des-one']}</div>
                  <div> · {lang['cto-des-two']}</div>
                  <div> · {lang['cto-des-three']}</div>
                  <div> · {lang['cto-des-four']}</div>
                </div>
                <div className='name-job'>
                  <div className='name'>
                    {lang['Richard']}
                  </div>
                  <div className='job'>
                    {lang['co-founder-and-cto']}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className='team-box-figure-info two'>
          <div className='figure-info'>
            <div className='figure-logo jason'>

            </div>
            <div className='figure-describe'>
              <div className='figure-describe-box'>
                <img src={teamDesLogo} alt='' />
                <div className='describe-box'>
                  <div> · {lang['jason-des-one']}</div>
                  <div> · {lang['jason-des-two']}</div>
                  <div> · {lang['jason-des-three']}</div>
                  <div> · {lang['jason-des-four']}</div>
                </div>
                <div className='name-job'>
                  <div className='name'>
                    {lang['Jason']}
                  </div>
                  <div className='job'>
                    {lang['software-engineer']}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='figure-info'>
            <div className='figure-logo janice'>

            </div>
            <div className='figure-describe'>
              <div className='figure-describe-box'>
                <img src={teamDesLogo} alt='' />
                <div className='describe-box'>
                  <div> · {lang['janice-des-one']}</div>
                  <div> · {lang['janice-des-two']}</div>
                  <div> · {lang['janice-des-three']}</div>
                  <div> · {lang['janice-des-four']}</div>
                </div>
                <div className='name-job'>
                  <div className='name'>
                    {lang['Janice']}
                  </div>
                  <div className='job'>
                    {lang['marketing-lead']}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className='team-box-figure-info three'>
          <div className='figure-info'>
            <div className='figure-logo daniel'>

            </div>
            <div className='figure-describe'>
              <div className='figure-describe-box'>
                <img src={teamDesLogo} alt='' />
                <div className='describe-box'>
                  <div> · {lang['daniel-des-one']}</div>
                  <div> · {lang['daniel-des-two']}</div>
                  <div> · {lang['daniel-des-three']}</div>
                </div>
                <div className='name-job'>
                  <div className='name'>
                    {lang['Daniel']}
                  </div>
                  <div className='job'>
                    {lang['product-lead']}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  )
}